import React from 'react';
import {  Icon } from 'antd';
import { Link } from 'react-router-dom';
import './CcMenu.css'



export class CcMenu extends React.Component{

	constructor (props){
		super(props);
		this.state = {
			current: 'DASHBOARD'
		}
	}

	handleClick = (e) => {
		    console.log('click ', e);
		    this.setState({
		      current: e.key,
		    });
		  }

	componentDidMount = ()=>{
		var current = this.props.location.pathname;
		
		 // eslint-disable-next-line default-case
		 switch (current) {
		 	case "/admin/jobdetails" : this.setState({  current: "JOB DETAILS" });
		 			break;
		 	case "/admin/clientdetails" : this.setState({ current: "CLIENT DETAILS", });
		 			break;
		 	case "/admin/invoiceprocess" : this.setState({ current: "INVOICE", });
		 			break;
		 	case "/admin/UserDetails" : this.setState({ current: "USER DETAILS", });
		 			break;
		 }
	}

	render(){

		return ( <div className={"menubar"}>
			<ul >
				<li >
		          <Link to={this.props.match.url}>  <Icon type="plus-square" />  DASHBOARD  </Link>
				</li>
		        <li >
		          <Link  to = { this.props.match.url + '/jobdetails'}> <Icon type="clock-circle" />JOB DETAILS </Link>
				  </li>
		        <li>
		          <Link to= { this.props.match.url + '/clientdetails'}>  <Icon type="user" />CLIENT DETAILS </Link>
		        </li>
		        
		        <li>
		          <Link to= { this.props.match.url + '/invoiceprocess'}>  <Icon type="bars" />INVOICE </Link>
		        </li>

		         <li>
		          <Link to= { this.props.match.url + '/UserDetails'}>  <Icon type="user" />USER DETAILS </Link>
				</li>
			</ul>
		 </div> )
	}
};